.dot-spinner {
	margin: auto 0;
	width: 100%;
	text-align: center;
	height: 100px;
	display: flex;
	justify-content: center;
	align-items: center;

	& > div {
		width: 18px;
		height: 18px;

		border-radius: 100%;
		display: inline-block;
		animation: sk-bouncedelay 1.4s infinite ease-in-out both;
	}
	& .bounce1 {
		animation-delay: -0.32s;
	}
	& .bounce2 {
		animation-delay: -0.16s;
	}
}
@keyframes sk-bouncedelay {
	0%,
	80%,
	100% {
		transform: scale(0);
	}
	40% {
		transform: scale(1);
	}
}
